.form{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 10px;
}

.formLabel {
    width: 300px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
}

.formTitle {
    font-size: 16px;
    font-weight: 700;
}

.formInput {
    width: 200px;
    height: 25px;
    border: 1px solid black;
    border-radius: 6px;

    font-size: 18px;
    background: transparent;
    
}


.formInput:hover,
.formInput:focus {
    border: 2px solid black;
    background: transparent;
}

.saveButton {
    margin-top: 20px;
    width: 100px;
    height: 30px;
    background-color: #fff;
    border: 1px solid black;
    border-radius: 10px;
    background: transparent;
}

.saveButton:hover,
.saveButton:focus {
    border: 1px solid black;
    background-color: black;
    color: #fff;

}
